<template>
  <div id="app">
    <ele-header :seller=content.seller></ele-header>
    <ul class="nav df">
      <!-- 路由有已定义好的类 -->
      <li><router-link to="/">商品</router-link></li>
      <li><router-link to="/comments">评价</router-link></li>
      <li><router-link to="/seller">商家</router-link></li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
import "./assets/less/base.less";
import "./assets/css/font.css";
import Header from "./components/Header/Header";
export default {
  name: 'app',
  data(){
    return {
      content:{}
    }
  },
  created(){
    let content=require('../data.json');//函数内部使用require，外部才可以使用import
    this.content=content;
    document.getElementsByTagName('html')[0].style.fontSize=document.documentElement.offsetWidth/10+"px";
  },
  components:{
    'ele-header':Header
  }
}
</script>

<style lang="less" scoped>
  @import "./assets/less/_base.less";
  .nav{
    .h(80);.lh(80);
    text-align:center;
    background-color:#fff;
    z-index:1;
    .fs(28);
    border-bottom:1px solid rgba(7,17,27,0.2);
    li{
      flex:1;
      a{
        display:block;
        width:100%;height:100%;
        color:rgb(77,85,93);
        &.router-link-exact-active{
          color:rgb(240,20,20);
        }
      }
    }
  }
</style>



