<template>
	<transition name="slide">
		<div @click="changeShow" class="buy">
			<div @click.stop class="cart">
				<h4 class="df aic jcsb"><span>购物车</span><i @click="clear">清空</i></h4>
				<div class="slider" id="slider">
					<ul>
						<li v-for="item in buyList" v-show="item.inCart" class="df jcsb aic">
							<div class="left df jcsb">
								<h5>{{item.name}}</h5>
								<span>¥<strong>{{item.price}}</strong></span>
							</div>
							<addcart @add="add(item)" @sub="sub(item)" :food=item></addcart>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</transition>
</template>
<script>
	require ('../../assets/js/iscroll-probe.js');
	import Addcart from "../Addcart/Addcart";
	export default {
		props:{
			buyList:{
				type:Array,
				required:true
			}
		},
		components:{
			Addcart
		},
		methods:{
			add(value){
				this.$emit('add',value);
			},
			sub(value){
				this.$emit('sub',value);
			},
			changeShow(){
				this.$emit('changeShow');
			},
			clear(){
				this.$emit('clear');
			}
		},
		mounted(){
			this.slider=new IScroll('#slider');
		}
	}
</script>
<style lang="less" scoped>
	@import "../../assets/less/_base.less";
	.slide-enter,.slide-leave-active{opacity:0;}
	.slide-enter-active,.slide-leave-active{transition:all .5s;}
	.buy{
		position:fixed;
		left:0;right:0;
		top:0;
		.bottom(94);
		background-color:rgba(7,17,27,0.8);
		.cart{
			position:fixed;
			left:0;right:0;
			.bottom(94);
			.maxh(520);
			background-color: #fff;
			h4{
				.pl(36);.pr(36);
				.h(80);
				border-bottom:1px solid rgba(7,17,27,.1);
				background-color: #f3f5f7;
				.fs(28);color:rgb(7,17,27);
				i{
					.fs(24);color:rgb(0,160,220);
				}
			}
			.slider{
				.maxh(440);
				overflow:hidden;
			}
			ul{
				.pl(36);.pr(36);
				li{
					box-sizing:border-box;
					border-bottom:1px solid rgba(7,17,27,.1);
					&:last-child{
						border-bottom:1px solid transparent;
					}
					.h(96);
					.left{
						flex:1;
						h5{
							.fs(28);
							color:rgb(7,17,27);
						}
						span{
							.mr(24);
							color:rgb(240,20,20);
							strong{
								.fs(28);
								.ml(4);
							}
						}
					}
				}
			}
		}
	}
</style>


