<template>
	<div class="goods">
		<div class="df">
			<div class="goods_left">
				<ul>
					<li @tap="changeSelected(index)" ref="aLis" :class="{'focus':index==selected}" v-for="(value,index) in content.goods">
						<p class="df aic">{{value.name}}</p>
					</li>
				</ul>
			</div>
			<div class="goods_right">
				<div>
					<dl ref="aDls" v-for="value in content.goods">
						<dt><span></span>{{value.name}}</dt>
						<dd v-for="val in value.foods" @tap="setGood(val)">
							<div class="line df">
								<img :src="val.icon" alt="美食">
								<div class="content">
									<h5>{{val.name}}</h5>
									<p v-if="val.description">{{val.description}}</p>
									<p>月售{{val.sellCount}}份　好评率{{val.rating}}</p>
									<p class="price">¥<strong>{{val.price}}</strong>　<del v-if="val.oldPrice"><b>¥{{val.oldPrice}}</b></del></p>
									<div class="addcart">
										<addcart @add="add(val)" @sub="sub(val)" :food=val></addcart>
									</div>
								</div>
							</div>
						</dd>
					</dl>
				</div>
			</div>
		</div>
		<detail :good=good v-show="showDetail" @close="showDetail=false"></detail>
		<div class="cart" @click="isShow">
			<cart :content=content :buyList=buyList></cart>
		</div>
		<buy :buyList=buyList @clear="clear" @changeShow="changeShow" @add="add" @sub="sub" v-show="showBuy&&buyList.length"></buy>
	</div>
</template>

<script>
	require("../../assets/js/iscroll-probe.js");
	import Cart from "../Cart/Cart";
	import Detail from "../Detail/Detail";
	import Addcart from "../Addcart/Addcart";
	import Buy from "../Buy/Buy";
	export default {
		data(){
			return {
				content:{},
				selected:0,
				listHeights:[],
				good:{},
				showDetail:false,
				buyList:[],
				showBuy:false
			}
		},
		components:{
			Cart,
			Detail,
			Addcart,
			Buy
		},
		created(){
			let content=require('../../../data.json');
			this.content=content;
		},
		methods:{
			changeSelected(index){
				this.selected=index;
				this.list.scrollToElement(this.$refs.aDls[this.selected],1000);
			},
			setGood(value){
				this.good=value;
				this.showDetail=true;
			},
			isShow(){
				this.buyList.length&&(this.showBuy=!this.showBuy);
			},
			add(value){
				if(this.buyList.some(item=>item===value))
					value.inCart++;
				else{
					this.buyList.unshift(value);
					this.$set(value,'inCart',1);
				}

			},
			sub(value){
				value.inCart>=1&&value.inCart--;
				value.inCart==0&&this.buyList.splice(this.buyList.findIndex(item=>item==value),1);
				this.buyList.length==0&&(this.showBuy=false);
			},
			changeShow(){
				this.buyList.length&&(this.showBuy=!this.showBuy);
			},
			clear(){
				let len=this.buyList.length,i=0;
				while(len--){
					this.buyList[len].inCart=0;
					this.buyList.splice(len,1);
				}
				this.showBuy=false;
			}
		},
		mounted(){
			this.nav=new IScroll(".goods_left",{mouseWheel:true,tap:true});
			this.list=new IScroll(".goods_right",{probeType:3,mouseWheel:true,tap:true});
			let _this=this;
			[...this.$refs.aDls].forEach(item=>this.listHeights.push(item.offsetTop));
			this.list.on('scroll',function(){
				let distance=Math.abs(this.y);
				_this.listHeights.forEach((item,index)=>(distance>=item)&&(_this.selected=index));
				_this.nav.scrollToElement(_this.$refs.aLis[_this.selected]);
			});
		}
	}
</script>

<style lang="less" scoped>
	@import "../../assets/less/_base.less";
	.goods{
		position:fixed;
		left:0;right:0;
		.top(349);.bottom(94);
		&>div.df{height:100%;}
		.goods_left{
			height:100%;overflow: hidden;
			.w(160);
			background-color:#f3f5f7;
			color:rgb(77,85,93);
			li{
				.pl(24);.pr(24);
				.h(108);
				p{
					border-bottom:1px solid rgba(7,17,27,.1);
					height:100%;
				}
				&.focus{
					background-color: #fff;
					color:#333;
				}
				&.focus p{
					border-bottom:1px solid transparent;
				}
			}
		}
		.goods_right{
			height:100%;overflow: hidden;
			flex:1;
			dt{
				position: relative;
				.pl(28);.h(52);.lh(52);
				color:rgb(147,153,159);
				background-color: #f3f5f7;
				span{
					position:absolute;
					height:100%;
					width:3px;
					left:0;top:0;
					background-color:#d9dde1;
				}
			}
			dd{
				.p(36,36,0,36);
				.line{border-bottom:1px solid rgba(7,17,27,.1);.pb(36)}
				&:last-child .line{
					border-bottom:0 none;
				}
				img{
					.w(114);.h(114);
					.bra(6);
				}
				.content{
					flex:1;
					.ml(20);
					h5{
						.fs(28);.lh(28);
						color:rgb(7,17,27);
						.mt(4);
					}
					p{
						.lh(24);.mt(16);
					}
					.price{
						color:rgb(240,20,20);
						.mt(18);
						strong{
							.fs(28);
						}
					}
					position: relative;
					.addcart{
						position:absolute;
						right:0;.bottom(-4);
					}
				}
			}
		}
		.cart{
		    position: fixed;
		    left:0;right:0;
		    bottom:0;
		    z-index:2;
		}
	}
</style>


