<template>
	<div @tap.stop class="addcart df aic jcsb">
		<transition name="slide">
			<i @click="sub" v-if="food.inCart" class="sub"><span>-</span></i>
		</transition>
		<transition name="fade">
		<p v-show="food.inCart">{{food.inCart}}</p>
		</transition>
		<i @click="add" class="add"><span>+</span></i>
	</div>
</template>
<script>
	export default {
		props:{
			food:{
				type:Object,
				required:true
			}
		},
		methods:{
			add(){
				this.$emit('add');
			},
			sub(){
				this.$emit('sub');
			}
		}
	}
</script>
<style lang="less" scoped>
	@import "../../assets/less/_base.less";
	.slide-enter,.slide-leave-active{transform:translateX(48px) rotate(720deg);}
	.slide-enter-active,.slide-leave-active{transition:all 0.5s;}
	.addcart{
		.w(144);.h(48);.lh(48);
		position: relative;
		i{
			position:absolute;
			top:0;
			.w(48);height:100%;
			text-align:center;
			border-radius:50%;
			.fs(34);
			span{
				position: relative;
			}
			&.sub{
				right:48px;
				box-sizing:border-box;
				.bd(4,rgb(0,160,220));
				span{
					.bottom(6);
				}
				color:rgb(0,160,220);
			}
			&.add{
				right:0;
				color:#fff;
				background-color:rgb(0,160,220);
			}
		}
		p{
			width:100%;
			.h(48);
			text-align: center;
		}
	}
</style>


