<template>
	<div @click="" class="cart df">
		<div class="left df">
			<div class="outer">
				<div class="bg">
					<div :class="{'focus':buyCount}" class="inner">
						<i class="iconfont icon-gouwuche"></i>
						<span v-show="buyCount">{{buyCount}}</span>
					</div>
				</div>
			</div>
			<div :class="{focus:buyCount}" class="total df aic">
				<p class="df aic"><strong>¥{{total}}</strong><span></span><i v-show="total<content.seller.minPrice">另需配送费¥{{content.seller.deliveryPrice}}</i></p>
			</div>
		</div>
		<div @click.stop class="right">
			<p v-show="!buyCount">¥{{content.seller.minPrice}}起送</p>
			<p class="gobuy" v-show="buyCount">结算</p>
		</div>
	</div>
</template>
<script>
	export default {
		props:{
			content:{
				type:Object,
				required:true
			},
			buyList:{
				type:Array,
				required:true
			}
		},
		computed:{
			buyCount(){
				let num=0;
				this.buyList.forEach(item=>num+=item.inCart);
				return num;
			},
			total(){
				let num=0;
				this.buyList.forEach(item=>num+=item.inCart*item.price);
				return num;
			}
		}
	}
</script>
<style lang="less" scoped>
	@import "../../assets/less/_base.less";
	.cart{
		.h(94);
		.left{
			background-color:#141d27;
			flex:1;
			.outer{
				position: relative;
				.w(112);
				.ml(24);.mr(24);
				.bg{
					background-color:#141d27;
					.w(112);.h(112);
					position:absolute;
					.bottom(4);
					border-radius:50%;
					span{
						position: absolute;
						.right(-10);.top(-14);
						.h(40);.w(40);.lh(40);
						color:#fff;
						border-radius:50%;
						text-align:center;
						background-color:red;
					}
				}
				.inner{
					.w(88);.h(88);
					border-radius:50%;
					.bd(12,#141d27);
					position:absolute;
					background-color:rgba(255,255,255,.2);
					text-align:center;
					.lh(82);
					i.icon-gouwuche{
						.fs(48);color:rgba(255,255,255,.4);
					}
					&.focus{
						background-color:#00a0dc;
						i.icon-gouwuche{
							color:#fff;
						}
					}
				}
			}
			.total{
				p{
					.lh(48);
					strong{
						.fs(32);
						.w(60);
					}
					span{
						.h(48);width:1px;
						background-color:rgba(255,255,255,.1);
						.ml(24);.mr(24);
					}
				}
				&.focus p strong{
					color:#fff;
				}
			}
		}
		.right{
			text-align:center;
			.lh(94);.fs(24);.w(210);
			font-weight: bold;
			color:rgba(255,255,255,.4);
			background-color:#2b333b;
			.gobuy{
				background-color:#00b43c;
				color:#fff;
			}
		}
	}
</style>


