<template>
	<div class="header">
		<div class="content df">
			<img class="img" :src="seller.avatar" alt="">
			<div class="right df fdc jcsb">
				<h3><a href=""><img src="../../assets/img/brand@2x.png" alt=""></a>{{seller.name}}</h3>
				<span>{{seller.description}}/{{seller.deliveryTime}}分钟可送达</span>
				<p><a href=""><img src="../../assets/img/decrease_1@2x.png" alt=""></a>{{seller.supports[0].description}}<i @click="show=true">{{seller.supports.length}}个 &gt;</i></p>
			</div>
		</div>
		<div @click="show=true" class="bulletin df aic">
			<p class="nowrap"><img src="../../assets/img/bulletin@2x.png" alt="">{{seller.bulletin}}</p><i>&gt;</i>
		</div>
		<div class="bg">
			<img :src="seller.avatar" alt="">
		</div>
		<popup @close="show=false" v-show="show" :seller=seller></popup>
	</div>
</template>
<script>
	import Popup from "../Popup/Popup"
	export default {
		data(){
			return {
				show:false
			}
		},
		props:{
			seller:{
				type:Object,
				required:true
			}
		},
		components:{
			Popup
		}
	}
</script>
<style lang="less" scoped>
@import "../../assets/less/_base.less";
	.header{
		.pt(48);
		color:#fff;
		position: relative;
		background-color:rgba(0,0,0,0.5);
		.content{
			.pr(24);
			.ml(48);
			.img{.w(128);.h(128);.bra(8);}
			.right{
				flex:1;
				.ml(32);
				h3{
					.fs(32);
					.lh(36);
					font-weight: bold;
					a{
						.mr(12);
						img{
							.h(36);
						}
					}
				}
				span{
					font-weight:200;
				}
				p{
					position: relative;
					.lh(24);
					img{
						.h(24);.mr(8);
						position: relative;top:1px;
					}
				}
				i{
					position: absolute;
					right:0;
					.top(-14);
					.p(14,16,14,16);
					.bra(48);
					background-color:rgba(0,0,0,0.2);
				}
			}
		}
		.bulletin{
			.mt(36);
			.h(56);
			.pl(24);.pr(24);
			background-color:rgba(7,17,27,0.2);
			p{
				.mr(8);
				img{
					.h(30);.mr(8);
					position: relative;
				}
			}
		}
		.bg{
			position:absolute;left:0;top:0;
			width:100%;height:100%;
			filter:blur(10px);
			z-index:-1;
			img{
				width: 100%;height:100%;
			}
		}
	}
</style>


