<template>
	<transition name="slide">
		<div class="good_detail">
			<div class="slider">
				<div>
					<div @tap="closeDetail" class="close"><span>&lt;</span></div>
					<div class="pic"><img :src="good.image" alt=""></div>
					<div class="content">
						<h4>{{good.name}}</h4>
						<p class="sell">月售{{good.sellCount}}份　好评率{{good.rating}}</p>
						<p class="price"><span>¥<strong>{{good.price}}</strong></span>　<del v-if="good.oldPrice"><b>¥{{good.oldPrice}}</b></del><i class="add_cart">加入购物车</i></p>
					</div>
					<div v-if="good.info" class="intro">
						<h5>商品介绍</h5>
						<p>{{good.info}}</p>
					</div>
					<div class="comments">
						<h5>商品评价</h5>
						<p class="sorts">
							<i @tap="current=0" :class="{'focus':current==0}">全部 <span>33</span></i>
							<i @tap="current=1" :class="{'focus':current==1}">推荐 <span>22</span></i>
							<i @tap="current=2" :class="{'focus':current==2}">吐槽 <span>11</span></i>
						</p>
						<div>
							<p>我是留言</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	require('../../assets/js/iscroll-probe.js');
	export default {
		data(){
			return {
				current:0
			}
		},
		props:{
			good:{
				type:Object,
				required:true
			}
		},
		methods:{
			closeDetail(){
				this.$emit('close');
			}
		},
		updated(){
			!this.silder&&(this.silder=new IScroll('.slider',{tap:true,click:true}));
		}
	}
</script>

<style lang="less" scoped>
	@import "../../assets/less/_base.less";
	.slide-enter,.slide-leave-active{opacity:0;transform:translateX(100%);}
	.slide-enter-active,.slide-leave-active{transition:all .5s;}
	.good_detail{
		position: fixed;
		left:0;right:0;
		top:0;
		.bottom(94);
		background-color:#f3f5f7;
		.slider{
			//height:100%;
			height:620px;
			width:100%;
			overflow: hidden;
		}
		.close{
			position: fixed;
			color:#fff;
			.w(110);.h(110);
			.fs(60);
			.lh(100);
			text-align:center;
			z-index:1;
		}
		.pic{
			.h(750);width:100%;
			text-align:center;
			position: relative;
			img{
				height:100%;
				position:absolute;
				left:50%;top:0;
				transform:translateX(-50%);
			}
		}
		.content,.intro,.comments{
			.p(36,36,36,36);
			border-top:1px solid rgba(7,17,27,.1);border-bottom:1px solid rgba(7,17,27,.1);
			.mt(32);
			background-color:#fff;
		}
		.content{
			.mt(0);
			h4{
				.fs(28);
				color:rgb(7,17,27);
				.lh(28);
				font-weight: bold;
			}
			.sell{
				.mt(16);
				color:rgb(147,153,159);
			}
			.price{
				.mt(36);
				position: relative;
				span{
					strong{.fs(32);}
					color:rgb(240,20,20);
				}
				.add_cart{
					position:absolute;
					right:0;
					.bottom(-4);
					.p(12,24,12,24);
					border-radius:100px;
					background-color:rgb(0,160,220);
					color:#fff;
				}
			}
		}
		.intro{
			h5{
				.fs(28);
				color:rgb(7,17,27);
				.mb(12);
			}
			p{
				.lh(48);
			}
		}
		.comments{
			h5{
				.fs(28);
				color:rgb(7,17,27);
			}
			.sorts{
				.mt(36);.mb(36);
				.h(64);.lh(64);
				color:rgb(77,85,93);
				i{
					background-color:rgba(0,160,220,.2);
					text-align: center;
					display:inline-block;
					.mr(16);
					.pl(24);.pr(24);
					span{
						.fs(16);
					}
					&.focus{
						color:#fff;
						background-color:rgb(0,160,220);
					}
				}
			}
		}
	}
</style>


