<template>	
	<transition name="popup">
		<div class="popup">
			<h3>{{seller.name}}</h3>
			<div class="star">
				<i class="golden"></i>
				<i class="golden"></i>
				<i class="golden"></i>
				<i class="golden"></i>
				<i :class="setStar"></i>
			</div>
			<h4><i></i>优惠信息<i></i></h4>
			<ul>
				<li :class="setIcon(value.type)" v-for="value in seller.supports">{{value.description}}</li>
			</ul>
			<h4><i></i>商家公告<i></i></h4>
			<p>{{seller.bulletin}}</p>
			<div @click="close" class="close">x</div>
		</div>
	</transition>
</template>
<script>
	export default {
		props:{
			seller:{
				type:Object,
				required:true
			}
		},
		computed:{
			setStar(){
				return this.seller.score==5?'golden':this.seller.score>=4.5?'half':'';
			}
		},
		methods:{
			close(){
				this.$emit('close');
			},
			setIcon(type){
				switch(type){
					case 0:
						return 'decrease';
					case 1:
						return 'discount';
					default:
						return 'special';
				}
			}
		}
	}
</script>
<style lang="less" scoped>
@import "../../assets/less/_base.less";
	.popup{
		position:fixed;
		top:0;bottom:0;
		left:0;right:0;
		z-index:1;
		color:#fff;
		.p(64,36,0,36);
		background-color:rgba(7,17,27,0.85);
		h3{
			.fs(32);
			font-weight: bold;
			.lh(32);
			text-align:center;
		}
		.star{
			.h(48);
			.mt(32);
			text-align: center;
			i{
				display:inline-block;
				.h(48);.w(48);
				.ml(20);.mr(20);
				background:url('../../assets/img/star24_off@3x.png') no-repeat 0 0;
				background-size:contain;
			}
			.golden{background-image:url('../../assets/img/star24_on@3x.png');}
			.half{background-image:url('../../assets/img/star24_half@3x.png');}
		}
		h4{
			.mt(56);.fs(28);.lh(28);.mb(48);
			text-align: center;
			font-weight: bold;
			i{
				display:inline-block;
				height:1px;
				background-color:rgba(255,255,255,.2);
				.w(200);
				position:relative;
				.bottom(10);
				&:first-child{
					.mr(24);
				}
				&:last-child{
					.ml(24);
				}
			}
		}
		li{
			.h(32);.lh(32);.mb(24);.pl(44);
		}
		.decrease{
			background:url('../../assets/img/decrease_1@2x.png') no-repeat left center;
			background-size:contain;
		}
		.discount{
			background:url('../../assets/img/discount_1@2x.png') no-repeat left center;
			background-size:contain;
		}
		.special{
			background:url('../../assets/img/special_1@2x.png') no-repeat left center;
			background-size:contain;
		}
		p{
			.lh(48);
		}
		.close{
			position:absolute;
			.bottom(120);
			left:50%;
			transform:translateX(-50%);
			.fs(64);
			color:rbga(255,255,255,0.5);
		}
	}
	.popup-enter-active{animation:popupIn .7s;}
	.popup-leave-active{animation:popupOut .7s;}
	@keyframes popupIn{
		0%{transform:scale(0);}
		35%{transform:scale(1.5);}
		100%{transform:scale(1);}
	}
	@keyframes popupOut{
		0%{transform:scale(1);}
		35%{transform:scale(1.5);}
		100%{transform:scale(0);}
	}
</style>


